import React from 'react';
import Tilt from 'react-tilt';
import brain from './brain.png';
import './Logo.css';
import '../../App.css';

const Logo = () => {
  return (

<div>
  <div className=' ma1 mt0 pa2'>
  <Tilt className="Tilt br2 shadow-2" options={{ max : 55 }} style={{width:130,height:130}} >
    <div className=" Tilt-inner pa3">
       <img style={{paddingTop: '5px'}} alt='logo' src={brain}/>
    </div>
  </Tilt>  
</div>
 <h1 className='gradient-text'>
  {'FACE DETECTION'}
 </h1> 
</div>  
     
);
}

export default Logo;