import React from 'react';
import './ImageLinkForm.css';

const ImageLinkForm = ({ onInputChange, onButtonSubmit }) => {
  return (
    
      <div >
      <p className='desc'>
        {'Copy any celebrity image address down below and see the moment of reveal for yourself!'}
      </p>
     
      
   
      <div className='center'>
        <div className='form center pa4 br3 shadow-5'>
          <input className='txtfieldwidth w-70 f4 pa2 center' type='text' onChange={onInputChange}/>
          <button
            className='btnwidth grow  link ph3 pv2 dib white bg-light-purple'
            onClick={onButtonSubmit}>Detect</button>
        </div>
      </div>
      </div>
   
  );
}

export default ImageLinkForm;